body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:after,
*:before {
  margin: 0;
}
.main {
    padding-left: 30px;
    padding-right: 30px;
    background-color: #eaeaea;
    min-height: calc(100vh - 60px);
}
.big-screen {
    height: 60px;
    background-color: #07393C;
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 569.09px) {
    .big-screen {
        display: none;
    }
}

@media (max-width: 569.6px) {
    .big-screen {
        display: none;
    }
}

.big-screen ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    line-height: 60px;
}

.small-screen {
    height: 60px;
    background-color: #07393C;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

@media (min-width: 570px) {
    .small-screen {
        display: none;
    }
}

.small-screen .nav-sidedrawer .toggleButton {
    cursor: pointer;
    width: 42px;
    height: 42px;
}

.small-screen .nav-sidedrawer .toggleButton span:first-child {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
    top: 15px;
}

.small-screen .toggleButton span:nth-child(2) {
    position: absolute;
    top: 25px;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
}

.small-screen .toggleButton span:nth-child(3) {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
    top: 35px;
}

.small-screen .nav-sidedrawer .sidedrawer {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    background-color: #dedede;
    width: 200px;
    height: 100%;
    box-shadow: 2px 2px 7px -1px black;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-close {
    text-align: right;
    cursor: pointer;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-close p {
    margin-right: 10px;
    font-weight: bolder;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
}
.nav-li {
    margin-left: 7px;
    margin-right: 7px;
    font-size: 14px;
}

@media (max-width: 569.09px) {
    .nav-li {
        margin: 0;
    }
    .nav-li:not(:first-child) {
        margin-top: 5px;
    }
}

.nav-li a {
    text-decoration: none;
    color: white;
}

.nav-li a:hover,
.nav-li a:active,
.nav-li a.active {
    color: #FFA400;
}

@media (max-width: 569.09px) {
    .nav-li a {
        display: inline-block;
        padding: 10px;
        width: 90%;
        font-weight: bolder;
        color: blue;
    }
    .nav-li:hover {
        background-color: #07393C;
    }
    .nav-li a:hover,
    .nav-li a:active {
        color: #FFA400;
    }
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .9);
    z-index: 98;
}

.error_message_parent {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    padding: 8px 15px;
    box-shadow: 3px 4px 4px -2px grey;
}

.error_message {
    font-size: 20px;
}
.Loader {
  color: #07393C;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  text-align: center;
  margin-top: 100px;
}
@-webkit-keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
@keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}

